import {fetch} from '@/utils/fetch';

/**
* 通讯录首页列表接口
 * @param {*}  params
 */
export const  getAllInfo= (params) => fetch({
  url: 'i/api/dept/getStaffDeptInfo',
  method: 'post',
  data: params
});
/**
 * 通讯录子部门以及部门成员列表接口
 * @param {*}  params
 */
export const  getSubMenu= (params) => fetch({
  url: 'i/api/dept/getDetail',
  method: 'post',
  data: params
});
/**
 * 通讯录员工详细信息列表接口
 * @param {*}  params
 */
export const  getCustomDetail= (params) => fetch({
  url: 'i/api/staff/customDetail',
  method: 'post',
  data: params
});

/**
 * 通讯录搜索列表接口
 * @param {*}  params
 */
export const  getSearch= (params) => fetch({
  url: 'i/api/staff/queryByPage',
  method: 'post',
  data: params
});



